/* App.css */

/* Navbar */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #D2FF72;
    padding: 15px;
    color: black;
  }
  
  .navbar ul {
    list-style: none;
    display: flex;
  }
  
  .navbar ul li {
    margin: 0 15px;
  }
  
  .navbar ul li a {
    color: black;
    text-decoration: none;
  }
  
  .navbar ul li a:hover {
    text-decoration: underline;
  }
  
  /* Footer */
  .footer {
    text-align: center;
    background: #222;
    color: white;
    padding: 10px;
    position: relative;
    bottom: 0;
    width: 100%;
  }
  
  /* Page Sections */
  section {
    padding: 50px;
    text-align: center;
  }
  
  /* Specific Section Backgrounds */
  .home {
    background: #f9f9f9;
  }
  
  .about {
    background: #e3e3e3;
  }
  
  .services {
    background: #f5f5f5;
  }
  
  .pastwork {
    background: #fff;
  }
  
  .contact {
    background: #ddd;
  }
  /* Services Section */
.services {
    text-align: center;
    padding: 40px 20px;
  }
  
  .service-buttons {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    justify-content: center;
    margin-top: 20px;
  }
  
  .service-button {
    background-color: white;
    border: 2px solid #ccc;
    border-radius: 20px;
    padding: 10px 15px;
    font-size: 16px;
    cursor: pointer;
    transition: all 0.3s ease;
    color: #333;
  }
  
  .service-button:hover {
    background-color: #007bff;
    color: white;
  }
  
  .service-button:active {
    background-color: #0056b3;
  }
  
  .service-button.selected {
    background-color: #007bff;
    color: white;
  }
  